
@import 'https://fonts.googleapis.com/css?family=Share+Tech+Mono';
.author {
  text-align: center;
  font-family: Share Tech Mono, sans;
  margin-top: 15px;

}

a {
  text-decoration: none;
  color: #00264d;
  line-height: 26px;
}
#root {
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;

}

#zero{
  width:50%;
}

#display{
  min-height: 40px;
  font-family: digital;
  font-size: 22px;
  color: orange;
  text-align: right;
  vertical-align: text-top;
  line-height: 20px;
  overflow-wrap: break-word;
  word-wrap: break-word; 
}
.output{
font-size: 40px;
  font-family: digital;
  color: white;
  text-align: right;
  line-height: 35px;
}

#clear{
width: 50%;
  background: #af2f2f;
}
#equals{
 
  width:50%;
  background:darkgreen;
  
  
}

#divide,#multiply,#subtract,#add{
  background:#6398ed;

  
  
  
}
#op-btns{

  display: inline-block;
  position:absolute;
  margin-top:65px;
  left:245px;
  

}

button{

  display:inline-block;
  position: relative;
  height: 65px;
  width: 80px;
  color: white;
  outline: 1px solid black;
  border: none;
  border-radius: 5%;
  background: #1e3559;
  font-family: Share Tech Mono, monospace;
  font-size: 20px;
  cursor: default;

}
button:hover {
  color: black;
  outline: .05em solid grey;
  z-index: 3;
 
}
#hmm{
  width:240px;
  
  
}
#calculator{
  
  
  margin-top:20px;
  border-radius:5px;
  border: 2px solid #47476b;
  padding: 5px; 
  background: #111823;
  width: 320px; 
  position: relative;
  

}


